<template>
  <div>
    <loading :active.sync="isLoading"></loading>
		<div class="text-right mt-3">
			<button class="btn btn-primary" @click="openModal(true)"></button>
		</div>
		<table class="table mt-4">
			<thead>
					<th width="120"></th>
					<th></th>
					<th width="100"></th>
					<th width="100"></th>
					<th width="120"></th>
					<th width="120"></th>
			</thead>
      <tbody>
        <tr v-for="(item) in products" :key="item.id">
          <td>{{item.category}}</td>
          <td>{{item.title}}</td>
          <td class="text-right">
              {{item.origin_price | currency}}
          </td>
          <td class="text-right">
              {{item.price | currency}}
          </td>
          <td>
            <span v-if="item.is_enabled == 1" class="text-success"></span>
            <span v-else></span>
          </td>
            <td>
              <button class="btn btn-outline-primary btn-sm" @click="openModal(false, item)"></button>
              <button class="btn btn-outline-danger btn-sm" @click="removeProductModal(item)"></button>
            </td>
        </tr>
      </tbody>
		</table>
    <Pagination :child-paginations="pagination" @changePage="getProducts"></Pagination>
    <!-- Add/Edit modal -->
		<div class="modal fade" id="productModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
			<div class="modal-dialog modal-lg" role="document">
				<div class="modal-content border-0">
					<div class="modal-header bg-dark text-white">
						<h5 class="modal-title" id="exampleModalLabel">
							<span>{{modalTitle}}</span>
						</h5>
						<button type="button" class="close" data-dismiss="modal" aria-label="Close">
							<span aria-hidden="true">&times;</span>
						</button>
					</div>
					<div class="modal-body">
						<div class="row">
							<div class="col-sm-4">
								<div class="form-group">
									<label for="image"></label>
									<input type="url" class="form-control" id="image"
										placeholder="" v-model="tempProduct.imageUrl">
								</div>
								<div class="form-group">
									<label for="customFile"> 
										<i class="fas fa-spinner fa-spin" v-if="status.fileUploading"></i>
									</label>
									<input type="file" id="customFile" class="form-control"
										ref="files" @change="uploadFile">
								</div>
								<!---->
								<img class="img-fluid" alt="" :src="tempProduct.imageUrl">
							</div>
							<div class="col-sm-8">
								<div class="form-group">
									<label for="title"></label>
									<input type="text" class="form-control" id="title"
										placeholder="" v-model="tempProduct.title">
								</div>

								<div class="form-row">
									<div class="form-group col-md-6">
										<label for="category"></label>
										<input type="text" class="form-control" id="category"
											placeholder="" v-model="tempProduct.category">
									</div>
									<div class="form-group col-md-6">
										<label for="unit"></label>
										<input type="text" class="form-control" id="unit"
											placeholder="" v-model="tempProduct.unit" autocomplete="off">
									</div>
								</div>

								<div class="form-row">
									<div class="form-group col-md-6">
									<label for="origin_price"></label>
										<input type="number" class="form-control" id="origin_price"
											placeholder="" v-model="tempProduct.origin_price">
									</div>
									<div class="form-group col-md-6">
										<label for="price"></label>
										<input type="number" class="form-control" id="price"
											placeholder="" v-model="tempProduct.price">
									</div>
								</div>
								<hr>

								<div class="form-group">
									<label for="description"></label>
									<textarea type="text" class="form-control" id="description"
										placeholder="" v-model="tempProduct.description"></textarea>
								</div>
								<div class="form-group">
									<label for="content"></label>
									<textarea type="text" class="form-control" id="content"
										placeholder="" v-model="tempProduct.content"></textarea>
								</div>
								<div class="form-group">
									<div class="form-check">
										<input class="form-check-input" type="checkbox" id="is_enabled" v-model="tempProduct.is_enabled" :true-value="1" :false-value="0">
										<label class="form-check-label" for="is_enabled">
											
										</label>
									</div>
								</div>
							</div>
						</div>
					</div>
					<div class="modal-footer">
						<button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
						<button type="button" class="btn btn-primary" @click="updateProduct"></button>
					</div>
				</div>
			</div>
		</div>
    <!-- remove modal -->
    <div class="modal fade" id="removeProductModal" tabindex="-1" role="dialog"
			aria-labelledby="exampleModalLabel" aria-hidden="true">
			<div class="modal-dialog" role="document">
				<div class="modal-content border-0">
					<div class="modal-header bg-danger text-white">
						<h5 class="modal-title" id="exampleModalLabel">
							<span></span>
						</h5>
						<button type="button" class="close" data-dismiss="modal" aria-label="Close">
							<span aria-hidden="true">&times;</span>
						</button>
					</div>
					<div class="modal-body">
						 <strong class="text-danger"> {{ tempProduct.title }}</strong> ()
					</div>
					<div class="modal-footer">
						<button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
						<button type="button" class="btn btn-danger" @click="removeProduct"></button>
					</div>
				</div>
			</div>
		</div>

  </div>
</template>

<script>
import $ from 'jquery';

export default {
  name: 'Products_backEnd',
  data () {
    return {
      products:[],
      tempProduct:{},
      isNew:false,
      isLoading:false,
      status:{
        fileUploading:false,
      },
      modalTitle:'',
      pagination:{},
    }
  },
  methods:{
    getProducts(page = 1){
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/products?page=${page}`;
      const vm = this;
      vm.isLoading = true;
      this.$http.get(api).then((re) => {
        if(re.data.success){
          vm.isLoading = false;
          vm.products = re.data.products;
          vm.pagination = re.data.pagination;
          vm.showpage();
        }
      })
    },//
    showpage(){
      console.log(this.pagination);
    },
    openModal(isNew, item){
      if(isNew){
        this.tempProduct = {},
        this.isNew = true;//updateProduct/
        this.modalTitle = '';
      }else{
        this.tempProduct = Object.assign({}, item);
        this.isNew = false;
        this.modalTitle = ` ${this.tempProduct.title}`;
      }
      $('#productModal').modal('show');
      // console.log(this.tempProduct);
    },
    updateProduct(){
      let api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/product`;
      let httpMethod = 'post';
      const vm = this;
      if(!vm.isNew){
        api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/product/${vm.tempProduct.id}`;
        httpMethod = 'put';
      }
      this.$http[httpMethod](api, {data:vm.tempProduct}).then((re)=>{
        if(re.data.success){
          $('#productModal').modal('hide');
          this.getProducts(vm.pagination.current_page);
        }
      })
    },
    removeProductModal(item){
      this.tempProduct = Object.assign({}, item);
      $('#removeProductModal').modal('show');
    },
    removeProduct(){
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/product/${this.tempProduct.id}`;
      this.$http.delete(api).then((re)=>{
        if(re.data.success){
          $('#removeProductModal').modal('hide');
          this.getProducts();
        }
      });
    },
    uploadFile(){
      const vm = this;
      vm.status.fileUploading = true;
      const uploadFile = this.$refs.files.files[0];
      const formData = new FormData();
      formData.append('file-to-upload', uploadFile);
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/upload`;
      this.$http.post(api, formData, {
        headers:{
          'Content-Type':'multipart/form-data',
        }
      }).then((re)=>{
        if(re.data.success){
          vm.$set(vm.tempProduct, 'imageUrl', re.data.imageUrl);
          vm.status.fileUploading = false;//loading
        }else{
          vm.status.fileUploading = false;
          this.$bus.$emit('message:push', re.data.message, 'danger');
        }
      })
    },
  },
  components:{

  },
  created(){
    this.getProducts();
  }
}
</script>

<style>

</style>



