<template>
  <div>
    <loading :active.sync="isLoading"></loading>
		<div class="text-right mt-3">
			<button class="btn btn-primary" @click="openModal(true)"></button><!-- @click="openModal(true)"-->
		</div>
    <table class="table mt-4">
			<thead>
					<th></th>
					<th></th>
					<th width="100"></th>
					<th width="120"></th>
					<th width="120"></th>
			</thead>
				<tbody>
					<tr v-for="item in coupons" :key="item.id">
						<td>{{item.title}}</td>
						<td>{{item.percent}}</td>
						<td>{{item.due_date}}</td>
						<td>
							<span v-if="item.is_enabled == 1" class="text-success"></span>
							<span v-else></span>
						</td>
							<td>
								<button class="btn btn-outline-primary btn-sm" @click="openModal(false, item)"></button>
								<button class="btn btn-outline-danger btn-sm" @click="openRemoveModal(item)"></button>
							</td>
					</tr>
				</tbody>
		</table>
    <Pagination :child-paginations="pagination" @changePage="getCoupons"></Pagination>
    <!-- couponModal -->
    <div class="modal fade" id="couponModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title" id="exampleModalLabel" v-if="isNew === false"> {{temCoupon.title}}</h5>
            <h5 class="modal-title" id="exampleModalLabel" v-if="isNew === true"></h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="form-group">
              <label for="title"></label>
              <input type="text" class="form-control" id="title"
                placeholder="" v-model="temCoupon.title">
            </div>
            <div class="form-group">
              <label for="code"></label>
              <input type="text" class="form-control" id="code"
                placeholder="" v-model="temCoupon.code">
            </div>
            <div class="form-group">
              <label for="due_date"></label>
              <input type="text" class="form-control" id="due_date"
                placeholder="" v-model="temCoupon.due_date">
            </div>
            <div class="form-group">
              <label for="percent"></label>
              <input type="number" class="form-control" id="percent"
                placeholder="" v-model="temCoupon.percent">
            </div>
            <div class="form-group">
              <div class="form-check">
                <input class="form-check-input" type="checkbox" id="is_enabled" v-model="temCoupon.is_enabled" :true-value="1" :false-value="0">
                <label class="form-check-label" for="is_enabled">
                  
                </label>
              </div>
            </div>
          </div>
          <div class="modal-footer">
						<button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
						<button type="button" class="btn btn-primary" @click="updateCoupon"></button>
					</div>
          
        </div>
      </div>
    </div>

    <!-- removeCouponModal -->
    <div class="modal fade" id="removeCouponModal" tabindex="-1" role="dialog"
			aria-labelledby="exampleModalLabel" aria-hidden="true">
			<div class="modal-dialog" role="document">
				<div class="modal-content border-0">
					<div class="modal-header bg-danger text-white">
						<h5 class="modal-title" id="exampleModalLabel">
							<span></span>
						</h5>
						<button type="button" class="close" data-dismiss="modal" aria-label="Close">
							<span aria-hidden="true">&times;</span>
						</button>
					</div>
					<div class="modal-body">
						 <strong class="text-danger">{{ temCoupon.title }}</strong> ()
					</div>
					<div class="modal-footer">
						<button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
						<button type="button" class="btn btn-danger" @click="removeCoupon"></button>
					</div>
				</div>
			</div>
		</div>

  </div>
</template>

<script> 
import $ from 'jquery';

export default {
  name: 'Coupons_backEnd',
  data(){
    return{
      temCoupon:{},//modal
      coupons:[],//
      pagination:{},
      isNew:false,
      isLoading:false,
    };
  },
  methods:{
    openModal(isNew, item){
      if(isNew){
        this.temCoupon = {};
        this.isNew = true;
      }else{
        this.temCoupon = Object.assign({}, item);
        this.isNew = false;
      }
      $('#couponModal').modal('show');
    },
    updateCoupon(){
      const vm = this;
      let api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/coupon`;
      let httpMethods = 'post';
      if(!vm.isNew){
        api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/coupon/${vm.temCoupon.id}`;
        httpMethods = 'put';
      }
      this.$http[httpMethods](api, {data:vm.temCoupon}).then((re) => {
        console.log('', re.data);
        if(re.data.success){
          $('#couponModal').modal('hide');
          this.getCoupons();
        }else{
          console.log('re.data.message');
          $('#couponModal').modal('hide');
          this.getCoupons();
        }
      });
    },
    getCoupons(page = 1){
      const vm = this;
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/coupons?page=${page}`;
      vm.isLoading = true;
      this.$http.get(api).then((re) => {
        console.log('', re.data);
        vm.isLoading = false;
        if(re.data.success){
          vm.coupons = re.data.coupons;
          vm.pagination = re.data.pagination;
        }else{
          console.log(re.data.message);
        }
      });
    },
    openRemoveModal(item){
      const vm = this;
      vm.temCoupon = Object.assign({}, item);
      $('#removeCouponModal').modal('show');
    },
    removeCoupon(){
      const vm = this;                          //openModalitemtemCouponid
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/coupon/${vm.temCoupon.id}`;
      this.$http.delete(api).then((re) => {
        console.log('', re.data);
        if(re.data.success){
          $('#removeCouponModal').modal('hide');
          vm.getCoupons();
        }else{
          console.log(data.re.message);
          $('#removeCouponModal').modal('hide');
          vm.getCoupons();
        }
      })
    }
  },
  created(){
    this.getCoupons();
  }
}
</script>



